import React, { FC } from 'react';
import { Tabs } from 'antd';
import { PoolItem, ProductItem } from '../../../../components';
import Pools from '../../../../data/pools.json';
import Products from '../../../../data/products.json';
import './Preview.scss';

const { TabPane } = Tabs;

const Preview: FC = () => (
  <Tabs className="Preview__tabs" defaultActiveKey="1">
    <TabPane tab={<h4>Latest Products</h4>} key="1">
      <div className="Preview__inner">
        {Products.map((item, index) => <ProductItem item={item} key={index+1} /> )}
      </div>
    </TabPane>
    <TabPane tab={<h4>Latest Pools</h4>} key="2">
      <div className="Preview__inner">
        {Pools.map((item, index) => <PoolItem item={item} key={index+1} /> )}
      </div>
    </TabPane>
  </Tabs>
);

export default Preview;